* {

  box-sizing: border-box;
}

html {
  height: 100%;
  background-image: linear-gradient(to bottom, #73735c, #af9e68);
}

.App {
  font-family: 'hooge 05_55 Cyr2', arial;
  gap: 10px;

}

.wrapper {
  margin: 30px auto 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  border: 2px solid #89b4b0;
  padding: 30px;
  width: 550px;
  height: 350px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

}
